button {
  display: inline-block;
  /* font-weight: bold; */
  /* letter-spacing: 2px; */
  padding: 8px 8px;
  text-decoration: none;
  text-transform: uppercase;
  transition: background 300ms;
  background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/134921/dark-bg.png);
  border: none;
}
.outline {
  color: white;
  overflow: hidden;
  position: relative;
}
.outline:before,
.outline:after,
.outline .lines:before,
.outline .lines:after {
  background-color: white;
  content: "";
  height: 2px;
  left: 0;
  position: absolute;
  top: 0;
  transition: transform 400ms;
  width: 100%;
}
.outline:before {
  bottom: 0;
  top: auto;
}
.outline .lines {
  display: block;
  height: 100%;
  left: 0;

  top: 0;
  width: 100%;
  z-index: 0;
}
.outline .lines:before,
.outline .lines:after {
  height: 100%;
  width: 2px;
}
.outline .lines:after {
  left: auto;
  right: 0;
}
button:hover:before {
  transform: translateX(-101%);
}
button:hover:after {
  transform: translateX(101%);
}
button:hover .lines:before {
  transform: translateY(-101%);
}
button:hover .lines:after {
  transform: translateY(101%);
}

body {
  background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/134921/dark-bg.png)
    repeat;
}
a {
  text-decoration: none;
  color: white;
}

button:focus {
  outline: none !important;
}
